#langPicker {
  text-align: center;
}
#langPicker span {
  line-height: 24px;
}
#langPicker a {
  background-image: url(icon/lang/en.png);
  background-repeat: no-repeat;
  background-size: 100%;
  display: inline-block;
  overflow: hidden;
  text-indent: -9999px;
  height: 24px;
  width: 24px;
  vertical-align: middle;
}
#langPicker .disabled {
  cursor: not-allowed;
}
#langPicker #lang-fr {
  background-image: url(icon/lang/fr.png);
}
#langPicker #lang-uk {
  background-image: url(icon/lang/en-uk.png);
}
#langPicker #lang-us {
  background-image: url(icon/lang/en-us.png);
}
html,
body,
input,
blockquote {
  font-family: 'Source Sans Pro', sans-serif;
}
@font-face {
  font-family: 'Source Sans Pro';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Source Sans Pro Regular'), local('SourceSansPro-Regular'), url(https://fonts.gstatic.com/s/sourcesanspro/v13/6xK3dSBYKcSV-LCoeQqfX1RYOo3aPw.ttf) format('truetype');
}
html,
body {
  font-size: 16px;
  margin: 0;
  padding: 0;
  min-height: 100%;
  width: 100%;
  cursor: default;
  word-wrap: break-word;
}
#progressbar {
  background-color: #EE5533;
  position: fixed;
  z-index: 312;
  width: 100%;
  height: 3px;
}
#toolbar {
  height: 35px;
  background-color: #24292e;
  color: white;
  display: flex;
  justify-content: center;
  vertical-align: middle;
  line-height: 24px;
}
#toolbar > * {
  margin: 5px;
  vertical-align: middle;
  line-height: 24px;
  height: 24;
  margin-left: 0;
}
#toolbar > *::before {
  content: ' — ';
  padding-right: 2px;
}
#toolbar > *:first-child::before {
  content: '';
}
#toolbar > * p {
  display: inline-block;
  margin: 0;
}
main #phaser canvas {
  display: block;
  margin: auto;
}
body {
  min-height: 100%;
  position: relative;
}
main {
  padding-bottom: calc(60px+35px+10px);
}
footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
}
footer {
  background-color: #3f4448;
  color: white;
  padding: 10px 0 30px 0;
  text-align: center;
  max-width: inherit;
}
footer a {
  color: lightblue;
}
footer a:hover {
  color: orange;
}
footer #mainFooter div#socialnetwork {
  position: absolute;
  right: 0;
  bottom: 0;
}
footer #mainFooter div#socialnetwork a#twittericon {
  background-image: url(out/twitter_bird_white.svg);
  background-repeat: no-repeat;
  background-position: center top;
  overflow-wrap: normal;
  background-size: 38px;
  margin: auto;
  display: block;
  padding-top: 38px;
  line-height: 20px;
  font-size: 20px;
  height: 38px;
  width: 108px;
}
footer #mainFooter div#socialnetwork a#twittericon:hover {
  background-image: url(out/twitter_bird_orange.svg);
}
blockquote {
  margin: 0 20px;
}
blockquote p {
  border: 1px dashed #5f5f5f;
  padding: 5px 20px;
  display: inline-block;
  text-align: center;
  margin: 0 auto;
}
blockquote span {
  display: block;
}
blockquote span.quote:before,
blockquote span.quote:after {
  font-family: sans-serif;
  font-weight: bold;
}
blockquote span.quote:before {
  content: "“";
}
blockquote span.quote:after {
  content: "”";
}
